body {
    position: relative;
}
.message {
    color: rgb(0, 179, 60);
    background: rgba(14, 218, 167, 0.26);
    font-size: 20px;
    border-style: solid;
    border-radius: 5px;
    padding: 10px;
    margin-bottom: 10px;
    position: absolute;
    top: 40%;
    left: 50%;
    transform: translateX(-50%);
}
.message.error {
    color: rgb(179, 0, 0);
    background: rgba(218, 14, 31, 0.26);
}